<template>
  <div>
    <div class="guanliyuan-top">
      <table class="right-content2-top w01">
        <tr>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td>
            <span class="top-span">ID</span>
            <a-input />
          </td>
          <td>
            <span class="top-span"></span>
            <a-input />
          </td>
          <td width="140">
            <span class="right">
              <a-button type="primary" icon="search"></a-button>
            </span>
          </td>
        </tr>
      </table>
    </div>
    <div class="guanliyuan-middle">
      <span>{{data.length}}</span>
      <div class="right">
        <a-upload
          class="inline-block"
          name="file"
          :multiple="true"
          action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
          @change="upload"
        >
          <a-button>
            <a-icon type="upload" />
          </a-button>
        </a-upload>
        <a-button icon="download"></a-button>
        <a-button icon="folder-add" @click="showModal('')"></a-button>
      </div>
    </div>
    <div class="right-content2-detail">
      <a-locale-provider :locale="zhCN">
        <a-table
          :rowSelection="rowSelection"
          :columns="columns"
          :dataSource="data"
          bordered
          :pagination="pagination"
          rowKey="2"
        >
          <div slot="">
            <span class="table-shenhe mr10" @click="showModal('')"></span>
            <span class="table-shenhe"></span>
          </div>
          <img style="width:50px;heigth:50px" slot="4" slot-scope="data" :src="data" />
        </a-table>
      </a-locale-provider>
    </div>
    <div>
      <a-locale-provider :locale="zhCN">
        <a-modal :title="modal" v-model="visible" @ok="handleOk">
          <div> <a-input />  </div>
          <div> <a-input /> <span></span> </div>
          <div>  <a-input />  <span>, !</span></div>
          <div> 
            <p class="mb-20">
              <a-upload
                class="inline-block"
                name="picture"
                :multiple="false"
                action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                @change="upload"
              >
                <a-button>
                  <a-icon type="upload" />
                </a-button>
              </a-upload>
            </p>
            <span>, ,  jpg, gif, png </span>
          </div>
          <div>
            
            <a-input />
          </div>
          <div>
            
            <a-input />
          </div>
        </a-modal>
      </a-locale-provider>
    </div>
  </div>
</template>

<script>
import zhCN from "ant-design-vue/lib/locale-provider/zh_CN"; // 
export default {
  name: "administrators",
  data() {
    return {
      zhCN,
      visible: false,
      modal:'',
      data: [],
      columns: [],
      pagination: {
        pageIndex: 1,
        pageSize: 8, // 
        // showQuickJumper:true,
        showSizeChanger: true, // 
        pageSizeOptions: ["8", "10", "20", "30", "40"], // 
        showTotal: total => ` ${total}  `, // 
        onShowSizeChange: (current, pageSize) => (this.pageSize = pageSize)
      }
    };
  },
  mounted() {
    // 
    this.columns = [
      {
        title: "ID",
        dataIndex: "0",

        scopedSlots: { customRender: "0" }
      },
      {
        title: "",
        dataIndex: "1",

        scopedSlots: { customRender: "1" }
      },
      {
        title: "",
        dataIndex: "2",

        scopedSlots: { customRender: "2" }
      },
      {
        title: "",
        dataIndex: "3",

        scopedSlots: { customRender: "3" }
      },
      {
        title: "",
        dataIndex: "4",
        scopedSlots: { customRender: "4" }
      },
      {
        title: "",
        dataIndex: "5",

        scopedSlots: { customRender: "5" }
      },
      {
        title: "",
        dataIndex: "6",

        scopedSlots: { customRender: "6" }
      },

      {
        title: "",
        dataIndex: "7",

        scopedSlots: { customRender: "" }
      }
    ];
    this.data = [];
    for (let i = 0; i < 30; i++) {
      this.data.push({
        0: i + 1,
        1: "",
        2: `135` + i + "1267349",
        3: "135" + i + "1267349",
        4: "https://gss0.bdstatic.com/94o3dSag_xI4khGkpoWK1HF6hhy/baike/c0%3Dbaike92%2C5%2C5%2C92%2C30/sign=4b1e0ff44da98226accc2375ebebd264/faf2b2119313b07e6a5add8902d7912396dd8c48.jpg",
        5: "2019-03-12 12:02:20",
        6: ""
      });
    }
  },
  computed: {
    rowSelection() {
      const { selectedRowKeys } = this;
      return {
        onChange: (selectedRowKeys, selectedRows) => {},
        getCheckboxProps: record => ({
          props: {
            disabled: record.name === "Disabled User", // Column configuration not to be checked
            name: record.name
          }
        })
      };
    }
  },
  methods: {
    handleChange(value, key, column) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target[column] = value;
        this.data = newData;
      }
    },
    upload(info) {
      if (info.file.status !== "uploading") {
        console.log(info.file, info.fileList);
      }
      if (info.file.status === "done") {
        this.$message.success(`${info.file.name} `);
      } else if (info.file.status === "error") {
        this.$message.error(`${info.file.name} `);
      }
    },
    showModal(param) {
      this.modal = param
      this.visible = true;
    },
    handleOk(e) {
      this.visible = false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



